import {
	mapState
} from 'vuex';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	computed: {
		...mapState(['token'])
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition: "fixed",
			headerTop: "0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0,
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			get_url: null,
			flagListPic: false,
			res_goodlist: null
		};
	},

	onLoad(options) {
		var last_data = JSON.parse(options.data);
		this.get_url = last_data.detail_url;
		this.setNavgatBar(last_data.title_name);
		uni.showLoading({
			title: '加载中'
		})
		this.getGoodList();

	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.loadData();
	},
	methods: {
		setNavgatBar(title) {
			uni.setNavigationBarTitle({
				title: title
			});
		},
		// lj获取商品
		getGoodList() {
			var now_token = this.token;
			uni.request({ // url: this.global_baseUrl + '/banners/',
				url: this.get_url,
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					var temp_data = res.data.good_class;
					var temp_goodsList = []
					if (temp_data.length == 0) {
						uni.showModal({
							title: '提示',
							content: '该分类下暂无商品！',
							showCancel: false,
							confirmText: '好的',
							success: function(res) {
								if (res.confirm) {
									uni.navigateBack({})
								}
							}
						});

					} else {
						temp_data.forEach(item => {
							try {
								var temp_goodItem = {
									image: item.describe_picture["0"].describe_picture,
									title: item.class_name,
									price: item.general_configure[0].price,
									sales: item.sales,
									url: item.url,
								}
								temp_goodsList.push(temp_goodItem)
							} catch (e) {
								uni.showModal({
									title: '提示',
									content: '该商品数据不完整！',
									showCancel: false,
									confirmText: '好的',
									success: function(res) {
										if (res.confirm) {
											uni.navigateBack({})
										}
									}
								});
							}

						})
						this.res_goodlist = temp_goodsList;
						this.loadData();
					}
				}
			});
			uni.hideLoading();
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = 'add', loading) {
			this.goodsList = [];
			//没有更多直接返回
			if (type === 'add') {
				if (this.loadingType === 'nomore') {
					return;
				}
				this.loadingType = 'loading';
			} else {
				this.loadingType = 'more'
			}
			// this.goodsList = this.res_goodlist;
			let goodsList = this.res_goodlist;
			if (type === 'refresh') {
				this.goodsList = [];
			}
			//筛选，测试数据直接前端筛选了
			if (this.filterIndex === 1) {
				goodsList.sort((a, b) => b.sales - a.sales)
			}
			if (this.filterIndex === 2) {
				goodsList.sort((a, b) => {
					if (this.priceOrder == 1) {
						return a.price - b.price;
					}
					return b.price - a.price;
				})
			}
			this.goodsList = this.goodsList.concat(goodsList);
			//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			// this.loadingType = this.goodsList.length > 20 ? 'nomore' : 'more';
			// if (type === 'refresh') {
			// 	if (loading == 1) {
			// 		uni.hideLoading()
			// 	} else {
			// 		uni.hideLoading()
			// 		uni.stopPullDownRefresh();
			// 		
			// 	}
			// }
		},
		//筛选点击
		tabClick(index) {
			uni.showLoading({
				title: '正在加载'
			});
			setTimeout(() => {
				if (this.filterIndex === index && index !== 2) {
					return;
				}
				this.filterIndex = index;
				if (index === 2) {
					this.priceOrder = this.priceOrder === 1 ? 2 : 1;
				} else {
					this.priceOrder = 0;
				}
				uni.pageScrollTo({
					duration: 300,
					scrollTop: 0
				})
				this.loadData('refresh', 1);
				uni.hideLoading()
			}, 1000)
		},
		//显示分类面板
		toggleCateMask(type) {
			uni.showLoading({
				title: '切换中...'
			})
			setTimeout(() => {
				this.flagListPic = !this.flagListPic;
				uni.hideLoading();
			}, 500)

		},
		//详情
		navToDetailPage(item_url) {
			uni.navigateTo({
				url: `/pages/product/product?item_url=${item_url}`
			})
		},
		navToDetailPageCopy(item_url) {
			uni.navigateTo({
				url: `/pages/product/product?item_url=${item_url}`
			})
		},
		stopPrevent() {}
	},
}
